import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom';

import App from './view/App';
import 'bootstrap-css-only/css/bootstrap.min.css';

ReactDOM.render(
    <HashRouter>
        <App/>
    </HashRouter>, 
    document.getElementById('root'));



